.flexible-modal {
  box-sizing: border-box;
  padding-top: 50px;
  position: absolute;
  z-index: 1;
  background: transparent;
  box-shadow: 0 4px 8px 0 rgba(0, 0, 0, 0.2), 0 6px 20px 0 rgba(0, 0, 0, 0.19);
}

/* Animation */

.modal-anim-enter {
  opacity: 0.00;
  transform: scale(0.7);
  transition: all 0.2s;
}

.modal-anim-enter.modal-anim-enter-active {
  opacity: 1;
  transform: scale(1);
}

.modal-anim-leave {
  opacity: 1;
  transform: scale(1);
  transition: all 0.2s;
}

.modal-anim-leave.modal-anim-leave-active {
  opacity: 0.00;
  transform: scale(0.7);
}

.flexible-modal-resizer {
  width:2px;
  height: 2px;
  position:absolute;
  right:0;
  bottom:0;
  cursor:se-resize;
  margin:3px;
  z-index: 20;
}

.flexible-modal-drag-area{
  height: 50px;
  display: grid;
  justify-content: left;
  align-items: center;
  font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
  background: #3f51b5;
  position:absolute;
  right:0;
  top:0;
  cursor:move;
}
.flexible-modal-drag-area div{
  padding-left: 12px;
  color: white;
  font-size: 20px;
  user-select: none;
}

.flexible-modal-rebound-btn{
    position: absolute;
    z-index: 1000;
    bottom: 0;
    width: 50px;
    height: 30px;
}

.flexible-modal-hidden{
    position: absolute;
    z-index: 1;
    background: white;
    animation-fill-mode: forwards;
    -webkit-animation-name: modalHidden; /* Safari 4.0 - 8.0 */
    -webkit-animation-duration: 1s; /* Safari 4.0 - 8.0 */
    animation-name: modalHidden;
    animation-duration: 1s;
}

.flexible-modal-rebound-btn-hidden{
    display: none
}

/* Safari 4.0 - 8.0 */
@-webkit-keyframes modalHidden {
    from   {}
    to  {left:0; top:800px; width: 50px; visibility: hidden}
}

/* Standard syntax */
@keyframes modalHidden {
    from   {}
    to  {left:0; top:800px; width: 50px; visibility: hidden}
}

.minimise-enter {
    opacity: 0.01;
    transform: scale(0.9) translateY(50%);
}
.minimise-enter-active {
    opacity: 1;
    transform: scale(1) translateY(0%);
    transition: all 300ms ease-out;
}
.minimise-exit {
    opacity: 1;
    transform: scale(1) translateY(0%);
}
.minimise-exit-active {
    opacity: 0.01;
    transform: scale(0.9) translateY(50%);
    transition: all 300ms ease-out;
}